import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Note } from '../shared/note';
import { StorageService } from '../shared/storage.service';

@Component({
  selector: 'app-notes-form',
  templateUrl: './notes-form.component.html',
  styleUrls: ['./notes-form.component.css']
})
export class NotesFormComponent implements OnInit {
  id: number;
  title: string;
  description: string;

  constructor(private storage: StorageService, private router: Router) { }

  ngOnInit() {
    this.getId(); // get if for new note
  }

  addNote() {
    this.storage.addNote(new Note(
      this.id,
      this.getDate(),
      (this.title) ? this.title : 'No title',
      (this.description) ? this.description : 'No description'
    ));

    this.goToBack();
  }

  getId(): void {
    this.id = Math.floor((1 + Math.random()) * 0x10000);
  }

  getDate(): string {
    return new Date().toString();
  }

  goToBack(): void {
    this.router.navigate(['']);
  }

}



