import { Injectable } from '@angular/core';
import { Note } from './note';

// let notesPromise = Promise.resolve(JSON.parse(localStorage.getItem(this.keyName)) || []);

@Injectable()
export class StorageService {

  keyName: string = 'notes'; 

  getNotes(): Promise<Note[]>{
    // localStorage.clear();
    return Promise.resolve(JSON.parse(localStorage.getItem(this.keyName)) || []);
  }

  getNote(id) {
    return this.getNotes().then(notes => notes.find(x => x.id === id));
  }

  addNote(note) {
    this.getNotes().then(result => {
      result.unshift(note);
      localStorage.setItem(this.keyName, JSON.stringify(result));
    });
  }

  deleteNote(id: number) {
    this.getNotes()
      .then(notes => notes.filter(x => x.id !== id))
      .then(result => localStorage.setItem(this.keyName, JSON.stringify(result)));
  }
}



